<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>粉丝管理</span>
      </div>
      <div class="text item">
          <div id="main" style="width:900px;height:600px"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  name: 'Fans',
  mounted () {
    this.getTu()
  },
  methods: {
    getTu () {
      var myChart = echarts.init(document.getElementById('main'))
      myChart.setOption({
        backgroundColor: '#2c343c',

        title: {
          text: 'Customized Pie',
          left: 'center',
          top: 20,
          textStyle: {
            color: '#ccc'
          }
        },

        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },

        visualMap: {
          show: false,
          min: 80,
          max: 600,
          inRange: {
            colorLightness: [0, 1]
          }
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '55%',
            center: ['50%', '50%'],
            data: [
              { value: 335, name: '男粉丝' },
              { value: 310, name: '女粉丝' },
              { value: 274, name: '小朋友' },
              { value: 235, name: '韩国人' },
              { value: 400, name: '日本人' }
            ].sort(function (a, b) { return a.value - b.value }),
            roseType: 'radius',
            label: {
              normal: {
                textStyle: {
                  color: 'rgba(255, 255, 255, 0.3)'
                }
              }
            },
            labelLine: {
              normal: {
                lineStyle: {
                  color: 'rgba(255, 255, 255, 0.3)'
                },
                smooth: 0.2,
                length: 10,
                length2: 20
              }
            },
            itemStyle: {
              normal: {
                color: '#c23531',
                shadowBlur: 200,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            },

            animationType: 'scale',
            animationEasing: 'elasticOut',
            animationDelay: function (idx) {
              return Math.random() * 200
            }
          }
        ]
      })
    }
  }
}
</script>

<style lang="less" scoped>
</style>
